.nice-select {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-sizing: border-box;
  clear: both;
  cursor: pointer;
  float: left;
  height: 38px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
  width: auto;
  background-color: #fff;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  outline: none;
  padding-left: 18px;
  padding-right: 30px;
  font-family: inherit;
  font-size: 14px;
  font-weight: normal;
  line-height: 36px;
  transition: all .2s ease-in-out;
  display: block;
  position: relative;
  text-align: left !important;
}

.nice-select:hover {
  border-color: #dbdbdb;
}

.nice-select:active, .nice-select.open, .nice-select:focus {
  border-color: #999;
}

.nice-select:after {
  content: "";
  height: 5px;
  pointer-events: none;
  transform-origin: 66% 66%;
  width: 5px;
  border-bottom: 2px solid #999;
  border-right: 2px solid #999;
  margin-top: -4px;
  transition: all .15s ease-in-out;
  display: block;
  position: absolute;
  top: 50%;
  right: 12px;
  transform: rotate(45deg);
}

.nice-select.open:after {
  transform: rotate(-135deg);
}

.nice-select.open .nice-select-dropdown {
  opacity: 1;
  pointer-events: auto;
  transform: scale(1)translateY(0);
}

.nice-select.disabled {
  color: #999;
  pointer-events: none;
  border-color: #ededed;
}

.nice-select.disabled:after {
  border-color: #ccc;
}

.nice-select.wide {
  width: 100%;
}

.nice-select.wide .nice-select-dropdown {
  left: 0 !important;
  right: 0 !important;
}

.nice-select.right {
  float: right;
}

.nice-select.right .nice-select-dropdown {
  left: auto;
